    import headTop from '@/components/base/header/head'
    export default {
        components: {
            headTop
        },
        data() {
            return {
                show: false,   // 审批人弹窗
                examineSection: null,  // 审批人部门
                examineName: null,   //  审批人姓名
                dutylist: ["厨房","服务"],  // 审批部门列表
                namelist: ["小明","小红"],  // 审批人员列表
                num: 0,    //  根据不同参数请求不同页面
                title: "我是标题",
                commonsLish: [
                    { title:"审批编号",txt:"54954549846546874354" },
                    { title:"所在部门",txt:"技术" },
                    { title:"请假类型",txt:"事假" },
                    { title:"开始时间",txt:"2015-5-4" },
                    { title:"结束时间",txt:"2015-5-6" },
                    { title:"请假事由",txt:"挖金矿" },
                    { title:"请假时长",txt:"7.5小时" }
                ],
            }
        },
        methods:{
            //            点击添加审批人弹窗确定按钮
            onConfirm(){
                console.log('我点击了确定',this.examineSection,this.examineName)
                if(this.examineSection == null && this.examineName == null){
                    this.examineNum = 0
                }else{
                    this.examineNum = 1
                }
            },
//            查看TA更多
            more(){
                console.log("敬请期待")
            },
            back(){
                console.log("我要撤回")
            },
            transter(){
                console.log("我要转审了")
                this.show = true
            }
        },
        created(){
            this.num = this.$route.params.index;
            this.title = this.$route.params.title;
            console.log(this.num)
        }
    }



